<script>
    import {term, crns} from './store.js';

    let plan_name = ''
	let username = '';
	let password = ''
	let preferred = true

	function handle()
    {
		if (!username || !password)
        {
            window.alert('Missing username or password!');
			return;
        }
		// fetch('http://localhost:8000/term_plan', {
        fetch('https://jcurda-api.herokuapp.com/term_plan', 
        {
			method: 'post',
			body: JSON.stringify
            ({
				"plan_name": plan_name,
				"username": username,
    			"password": password,
				"term": $term,
				"crns": $crns,
				"preferred": preferred
			})
		})
        .then((response) => 
            {
                if (!response.ok) 
                {
                    response.text()
                        .then(text => {window.alert(text); active.set('Select Courses');})
                }
                else 
                {
                    response.text()
                        .then(text => {window.alert(text);})
                }
            })
		;
	}
</script>

<div style="position: relative; max-height: calc(100vh - 5%); overflow: auto;">
    <h1>Export to Google Calendar</h1>
    <span>Learn how to import iCal file to Google Calender </span> <a href='https://support.google.com/calendar/answer/37118#import_to_gcal'>here</a> <br> <br>
    <button on:click={() => window.alert('this does nothing rn')}>Download</button>
    <hr>
    <h1>Upload term plan to RWeb</h1>
    <input type="text" placeholder="Term plan name" bind:value={plan_name} style="width: 50%"/> <br>
    <input type="text" placeholder="RWeb username" bind:value={username} style="width: 50%"/> <br>
    <input type="password" placeholder="RWeb password" bind:value={password} style="width: 50%"/> <br>
    <label style="padding:0 0 15px 0;"><input type="checkbox" bind:checked={preferred}/> Set current schedule as preferred term plan</label> 
    <button on:click={() => handle()}>Submit</button>
    <hr>
    <h1>Schedule crns as plaintext</h1>
    <textarea style="width: 50%; height:15em;">{[...new Set($crns)].join('\n')}</textarea>
</div>

<style>
    h1 {
		text-transform: uppercase;
		font-size: 1.8em;
		font-weight: 100;
        margin-top: 0;
        /* top: 100px; */
        margin-bottom: 10px;
    }

    button {
        text-transform: uppercase;
		font-size: 1.5em;
		font-weight: 100;
        margin-top: 0;
        top: 100px;
        margin-bottom: 10px;
        width: 30%;
        /* height: 10%; */
        font-size: 20px;
    }

    a:visited {
       color: mediumpurple;
    }
</style>