<script>
    import {term, courses} from './store.js';
    let term_codes = [];

    
    fetch('json/terms.json')
        .then((data) => data.json())
		.then((jsonData) => 
		{
            term_codes = jsonData;
            if (!$term)
                term.set(term_codes[0]['code']);
		});
    ;
    
    const setTerm = (code) => {
        term.set(code);
        courses.set([])
    }
</script>

{#each term_codes as term_, i (i)}
    <h1 class={$term == term_['code'] ? 'active' : ''} on:click={setTerm(term_['code'])}>{(term_["description"].indexOf("(") != -1) ? term_["description"].substring(0, term_["description"].indexOf('(') - 1) : term_["description"]}</h1>
{/each}

<style>
    h1 {
        cursor: pointer;
		font-size: 2em;
		font-weight: 100;
        margin-top: 0;
        -webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
    }
    
    .active {
        color: rgb(164, 149, 231);
    }
</style>