<script>
    import {theme, active, term, courses} from './store.js';
	import VirtualList from './VirtualList.svelte';
	import ListItem from './ListItem.svelte';
    
    export let items = [];
	fetch(`json/${$term}.json`)
        .then((data) => data.json())
		.then((jsonData) => 
		{
			jsonData.forEach((element) => 
			{
				items = [...items, 
				{
					// "key":element.id,
					"name": element.code,//`${element.code} | ${element.description}`,
					"content": element.description,
					"sendDispatch":element.code,
				}];
			});
		});
    ;

	let searchTerm = "";
	$: filteredList = items.filter(item => item.name.toLowerCase().indexOf(searchTerm.toLowerCase()) !== -1);
	
	let start, end;
	// let tempcourses = []
	function receiveDispatch(e) {
		let tempcourses = $courses;
		console.log(tempcourses.indexOf(e.detail.text))
		if (tempcourses.indexOf(e.detail.text) == -1)
			tempcourses = [...tempcourses, e.detail.text];
		if (tempcourses.length > 5)
		{
			window.alert('You can only select a maximum of 5 courses!');
			return;
		}
		console.log(tempcourses);
		courses.set(tempcourses);
	}

	function removeCourse(course) {
		let tempcourses = $courses;
		tempcourses.splice(tempcourses.indexOf(course), 1);
		courses.set(tempcourses);
	}

	function clear() {
		courses.set([])
	}
</script>
<div style='width: 100%;'>
	<div style='float:left; width:60%'>
		<h1 for="search" style='display:inline;'>Filter:&nbsp;</h1>
		<input type="text" id="search" bind:value={searchTerm} style='display:inline;width:50%;padding: 0.5%;' />
	</div>
	<div style='float:right; width:40%'>
		<h1>Selected Courses: </h1>
	</div>
</div>
<div style='width: 100%;'>
	<div class='container' style='float:left; width:60%;'>
		<div style='height:100%'>
			<VirtualList items={filteredList} bind:start bind:end let:item>
				<ListItem {...item} on:message={receiveDispatch} />
			</VirtualList>
		</div>
		<p style='margin:auto'>showing items {start}-{end}</p>
	</div>
	<div class='container' style='float:right; width:40%;'>
		<br>
		{#each $courses as course}
			<div class='card {$theme}'>
				<h2 on:click={removeCourse(course)}>{course}</h2>
				<!-- <span style='width:50%;'>{course}</span> -->
				<!-- <span class='removeBtn'>X</span> -->
			</div>
		{/each}
	</div>
	<h1 on:click={clear} style='float:right; width:40%; margin: 0 auto;'>Clear All Courses</h1>
</div>

<style>
	h1 {
		font-size: 2em;
		font-weight: 100;
		display:inline;
    }

	h2 {
		margin: auto;
		font-size: 20px;
	}

	.container {
		border-top: 2px solid #bdbdbd;
		min-height: 200px;
		height: calc(100vh - 15em);
		/* display: grid;  */
		grid-template-rows: calc(105%);
		width:100%;
		/*padding: 0; */
	}

	.card {
		cursor: pointer;
		position: relative;
		margin: 0.5em;
		/* padding: 0.5em 0.5em 0.5em 6em; */
		padding: 1em 0.5em 1em 0.5em;
		border-radius: 8px;
		box-shadow: 2px 2px 4px rgba(0,0,0,0.3);
		min-height: 2em;
		/* transition: 0.25s; */
		/* background: #454e56; */

		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
	}

	.light {
		background-color: var(--gray2);
		color: var(--gray6);
	}

	.dark {
		background-color: var(--gray5);
		color: var(--gray0);
	}

	.card::after {
		clear: both;
		display: block;
	}

	.card:hover.light {
		background: #6c79837a;
	}

	.card:hover.dark {
		background: #0000003d /* var(--gray4); */
	}

	* {
		text-align: center;
		transition: 0.15s;
	}
</style>