<svelte:head>
    <!-- <script src="./codebase/dhtmlxscheduler.js" on:load={fetchSchedules}></script> -->
    <!-- <script src="./codebase/ext/dhtmlxscheduler_limit.js"></script>  -->
    <!-- <link rel="stylesheet" href="./codebase/dhtmlxscheduler_terrace.css" type="text/css"> -->
</svelte:head>

<script>
    import {term, courses, active, crns} from './store.js';
    import Spinner from './LoadingSpinner.svelte'
    import Modal from './Modal.svelte'
    import TermPlan from './TermPlan.svelte'
    import { crossfade } from 'svelte/transition';
    let showModal = false;

    // function print() {
    //     $courses.split(',').forEach((element) => 
    //     {
    //         fetch(`https://jcurda-api.herokuapp.com/?term=${$term}&course=${element}`)
    //             .then((data) => data.json())
    //             .then((jsonData) => 
    //             {
    //                 console.log(jsonData);
    //             });
    //         ;
    //     });
    // }

    let loaded = false;
    let items = [];
    let idx = 0;
    function fetchSchedules() {
        if (!$courses.length)
        {
            window.alert("You have no couses selected!");
            active.set('Select Courses');
        }
        else
        {
            fetch(`https://jcurda-api.herokuapp.com/schedules?term=${$term}&courses=${$courses.join(',')}`)
                .then((response) => 
                {
                    if (!response.ok) 
                    {
                        response.text()
                            .then(text => {window.alert(text); active.set('Select Courses');})
                    }
                    else 
                    {
                        response.json()
                            .then((jsonData) => 
                            {
                                items = jsonData;
                                // console.log(items);
                                start();
                            })
                    }
                })
            ;
        }
    }
    
    function start(){
        scheduler.config.multi_day = true;
        scheduler.config.readonly = true;
        // scheduler.config.readonly_form = true;
        scheduler.config.start_on_monday = false;
        scheduler.config.first_hour = 7;
        scheduler.config.last_hour = 22;
        scheduler.config.hour_date="%h %A";
        scheduler.config.day_date="%l";
        // scheduler.config.hour_size_px=30;
		scheduler.init('scheduler_here',new Date(2020,0,0),"week");
        update();
        loaded = true;

        scheduler.attachEvent("onClick", function (id, e){
            //any custom logic here
            console.log('test')
            return true;
        });

    }

	function show_minical(){
		if (scheduler.isCalendarVisible())
            scheduler.destroyCalendar();
		else
			scheduler.renderCalendar({
				position:"dhx_minical_icon",
				date:scheduler.getState().date,
				navigation:true,
				handler:function(date,calendar){
					scheduler.setCurrentView(date);
					scheduler.destroyCalendar()
				}
			});
    }

    const typeIDX = (e) => {
        let num = e.target.value - 1
        if (!(num >= 0 && num <= items.length))
            return
        console.log(num)
        idx = num
        update()
    }

    const next = () => {
        console.log(idx);
        if (idx + 1 >= items.length)
            return;
        idx++;
        update();
    }

    const prev = () => {
        console.log(idx);
        if (idx <= 0)
            return;
        idx--;
        update();
    }

    const save = () => {
        // idx++;
        // update();
        console.log($term)
        console.log($courses)
        // fetch(`http://127.0.0.1:5000/schedules?term=${$term}&courses=${$courses}`)
        fetch(`https://jcurda-api.herokuapp.com/schedules?term=${$term}&courses=${$courses}`)
            .then((data) => console.log(data.json()))
            .then((jsonData) => 
            {
                console.log(jsonData)
            });
        ;
    }

    function update() {
        scheduler.clearAll();
        // scheduler.load("./common/events.json");
        scheduler.parse(items[idx][0]);
        crns.set(items[idx][1]);
    }

    fetchSchedules();
</script>

{#if !loaded}
    <Spinner />
    <h1>Loading Schedules...</h1>
{/if}
<div id="scheduler_here" class="dhx_cal_container" style='width:100%; height:{loaded?100:0}%;'>
    <Modal bind:showModal>
        <TermPlan/>
    </Modal>
    <div class='overlay'>
        <span class='invisible'>aa</span>
        <span>Showing schedule <input class="inpnum" type="number" value={idx+1} on:input={e=>typeIDX(e)} min=1 max={items.length}> of {items.length}</span>
        <span on:click={() => showModal=!showModal}>Save<br>Schedule</span>
        <span on:click={prev}>Previous<br>Schedule</span>
        <span on:click={next}>Next<br>Schedule</span>
        <span class='invisible'></span>
    </div>
    <div class="dhx_cal_navline"></div>
    <div class="dhx_cal_header"></div>
    <div class="dhx_cal_data"></div>
</div>


<style>
    h1 {
		font-size: 2em;
		font-weight: 100;
        margin-top: 0;
        top: 100px;
        margin-bottom: 10px;
    }

    /* body{
        margin:0px;
        padding:0px;
        height:100%;
        overflow:hidden;
   }    */

   .inpnum {
       width: 25%;
   }

   .dhx_cal_navline {
        visibility: hidden;
        margin: 0px;
        padding: 0px;
        height: 0px;
   }

   .overlay {
        display: flex;
        align-items:center;
        justify-content: space-between;
        flex-wrap: wrap;
        /* background: black; */
        height: 5em;
        z-index: 99;
        /* display: table;
        width: 100%;
        table-layout: fixed; */
   }

   .overlay span {
        cursor: pointer;
        display: table-cell;
        text-align: center;
        /* background: rgba(255, 255, 255, 0); */
        color: black;
        /* text-transform: uppercase; */
		font-size: 1.5em;
		/* font-weight: 0; */
    }

    .overlay span.invisible {
        background: transparent;
        color: transparent;
    }
</style>